.stake-table {
    width: 100%;
    border-collapse: collapse;
    color: #fff;
}

.stake-table th {
    font-size: 16px;
    background: #0B0D13;
    padding: 18px 15px;
    text-align: right;
    font-weight: normal;
}

.stake-table .head-pool {
    text-align: left;
}

.stake-table .head-roi {
}


.stake-table tr {
    cursor: pointer;
}

.stake-table td {
    background: #0B0D13;
}

.stake-table td {
    background: #12161F;
    padding: 12px 15px;
    text-align: right;
   
}

.stake-table h3 {
    margin: 0;
    font-weight: normal;
    font-size: 18px;
}
.stake-table p {
    margin: 0;
    color: #5B5B60;
    font-size: 16px;
}

.stake-table p:not(:first-child) {
    margin-top: 8px;
}

.stake-table .cell-pool {
    text-align: left;
}
.stake-table .cell-pool,
.stake-table .cell-per {
    
}

.stake-table .cell-pool .cell-inner {
    display: flex;
    align-items: center;
}
.stake-table .cell-pool .logo-wrapper {
    margin-right: 12px;
    
}

.stake-table .cell-pool .logo-wrapper  img {
    width: 36px;
}


.stake-table .cell-per .cell-inner {
    display: flex;
    align-items: center;
    justify-content:flex-end;
}




.window {
    width: 100%;
}

.back-bar {
    height: 70px;
    background: #0B0D13;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 36px;
    margin-bottom: 10px;
    border-radius: 4px;
}

.back-bar .back-btn  {
    color: #E91C41;
    font-size: 18px;
    cursor: pointer;
}
.back-bar .back-btn img {
    width: 15px;
    /* vertical-align: middle; */
    margin-right: 20px;
}

.reward-area {
    height: 130px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #0B0D13;
    padding: 0 36px;
    margin-bottom: 10px;
    border-radius: 4px;
}

.reward-area p {
    font-size: 22px;
    color: #E91C41;
    margin: 16px 0 0;
}
.reward-area label {
    color: #76757B;
    font-size: 14px;
}

.reward-area .reward-area-left {
    display: flex;
}

.reward-area .reward-area-right {
    display: flex;
    align-items: center;
} 

.reward-area .claim-btn {
    height: 40px;
    background: #E91C41;
    box-shadow: 0px 3px 4px 0px rgba(38, 48, 87, 0.06);
    border-radius: 5px;
    border: 1px solid #E91C41;
    font-size: 16px;
    color: #fff;
    cursor: pointer;
    margin-right: 24px;
    padding: 0 36px;
}
.reward-area .stake {
    margin-right: 64px;
}
.reward-area p {
    color: #fff;
}

.stake-area {
    display: flex;
}

.stake-form {
    width: 350px;
    /* height: 360px; */
    background: #0B0D13;
    border-radius: 4px;
    margin-right: 10px;
    padding: 10px 20px;
}

.tabs {
    display: flex;
    justify-content: space-around;
   
}

.tabs .tab-btn {
    width: 150px;
    padding: 20px 0;
    text-align: center;
    font-size: 16px;
    cursor: pointer;
    color: #75757B;
   
}

.tabs .tab-btn.active {
    border-bottom: 2px solid #E91C41;
    color: #FFFFFF;
}



.form-inner {
    background: #12161F;
    margin-top: 45px;
    padding: 20px 20px;
    
}

.form-inner .tip {
    display: flex;
    justify-content: space-between;
    margin-bottom: 12px;
    color: #76757B;
    font-size: 16px;
}


.form-inner .input-wrapper {
    display: flex;
    align-items: center;
}


.form-inner .input-wrapper input {
    border: 0;
    outline: none;
    background: transparent;
    color: #fff;
    font-size: 22px;
    width: 50%;
    flex-grow: 1;
    flex-shrink: 1;
}

.form-inner .input-wrapper .btn {
    width: 48px;
    height: 20px;
    line-height: 20px;
    text-align: center;
    background: #181D29;
    border-radius: 4px;
    color: #76757B;
    border: 0;
    display: inline-block;
    font-size: 14px;
    margin-left: 20px;
    flex-shrink: 0;
    cursor: pointer;
}

.form-inner .input-wrapper .unit {
    font-size: 22px;
    color: #fff;
    margin-left: 20px;
    flex-shrink: 0;
}

.stake-area .submit-btn {
    width: 100%;
    height: 40px;
    background: #E91C41;
    box-shadow: 0px 3px 4px 0px rgba(38, 48, 87, 0.06);
    border-radius: 5px;
    border: 1px solid #E91C41;
    color: #1E1E1E;
    font-size: 16px;
    color: #fff;
    margin: 50px auto 0;
    cursor: pointer;
    outline: none;
}


.stake-pool {
    background: #0B0D13;
    border-radius: 4px;
    flex: 1;
    padding: 32px 20px;
    
}

.stake-pool  .section-title {
    font-size: 16px;
    font-weight: normal;
    color: #FFFFFF;
    margin: 0;
}
.stake-pool .pool-list {
    margin: 0;
    padding: 0;
    list-style: none;
    /* margin-top: 46px; */
   
}

.stake-pool .pool-list li {
    padding: 16px 15px;
    color: #76757B;
    font-size: 14px;
    border-bottom: 1px solid #1D2129;
}

.stake-pool .pool-list .claim-row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 4px 15px;
}


.stake-pool .rewords{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.stake-pool .claim-btn {
    height: 32px;
    background: #E91C41;
    box-shadow: 0px 3px 4px 0px rgba(38, 48, 87, 0.06);
    border-radius: 3px;
    border: 1px solid #E91C41;
    color: #fff;
}

.rewords em {
    color:  #E91C41;
    font-style: normal;
    font-size: 16px;
}